import { Injectable } from '@angular/core';
import { Meta } from '@angular/platform-browser'
import { Http, Response, Headers, RequestOptions, Jsonp, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs';

import { ExchangeData } from '../../Modeles/Exchang/exchange-data'
import { RailsService } from '../Shared/rails.service';



// 外為オンラインAPI
const EXCHANGE_API = 'api/get_exchange_rate';



@Injectable()
export class ExchangeRateService {

    private canadaDollar;
    private usDollar;
    private ukpPound;
    private eur;

    public action;

    private loopTask;



    // コンストラクタ
    constructor(
        private http: Http,
        private rails: RailsService) { }



    ////////////////////////////////
    // API
    ////////////////////////////////

    // 為替取得スタート
    public start(): void {
        try {
            this.stop();
            this.getExchangeRate();
            Observable
                .interval(30000)
                .subscribe(() => this.getExchangeRate());
        }
        catch (ex) {
            console.log(ex);
        }


    }



    // 為替取得終了
    public stop(): void {
        try {
            if (this.loopTask !== null) {
                this.loopTask.onNext(false);
            }
        }
        catch (ex) {
            console.log(ex);
        }
    }



    ////////////////////////////////
    // Privateメソッド
    ////////////////////////////////

    // 外為レート取得
    private getExchangeRate() {
        return this.http.get(EXCHANGE_API)
            .subscribe(
            res => {
                console.log(res.text());
                // レスポンス
                if (res.status === 200) {
                    var arr = res.json() as [ExchangeData];
                    this.parseArr(arr);
                    this.doAction();
                } else {

                }
            },
            error => {
                console.log(error);
            }
            );
    }



    private parseArr(arr: [ExchangeData]) {
        arr.map(data => {
            if (ExchangeData.isCADJPY(data)) {
                this.canadaDollar = data.open
            } else if (ExchangeData.isUSDJPY(data)) {
                this.usDollar = data.open
            } else if (ExchangeData.isGBPJPY(data)) {
                this.ukpPound = data.open
            } else if (ExchangeData.isEURJPY(data)) {
                this.eur = data.open
            }
        });
    }



    private doAction() {
        try {
            if (this.action !== null) {
                this.action(this.canadaDollar, this.usDollar, this.ukpPound, this.eur)
            }
        }
        catch (ex) { }
    }
}



