import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { HttpModule, JsonpModule } from '@angular/http';

// lib
import { MdlModule } from '@angular-mdl/core'
import { MdlDatePickerModule } from '@angular-mdl/datepicker';
import { MdlExpansionPanelModule } from '@angular-mdl/expansion-panel';
import { MdlPopoverModule } from '@angular-mdl/popover';
import { MdlSelectModule } from '@angular-mdl/select';

// router
import { Routes, RouterModule } from '@angular/router';
import { routes } from './app.routing';


// My Class
import {AppComponent} from './Components/app/app.component';

import {PageComponent} from './Components/page/page.component';
import {HeaderComponent as PageLayoutHeaderComponent} from './Components/page/layout/header/header.component';
import {FooterComponent as PageLayoutFooterComponent} from './Components/page/layout/footer/footer.component';
import {HomeComponent as PageHomeComponent} from './Components/page/home/home.component';
import {ContactComponent as PageContactComponent} from './Components/page/contact/contact.component';
import {SignupComponent as PageSignupComponent} from './Components/page/signup/signup.component';
import {LoginComponent as PageLoginComponent} from './Components/page/login/login.component';
import {TermsComponent as PageTermsComponent} from './Components/page/terms/terms.component';
import {LawComponent  as PageLawComponent} from './Components/page/law/law.component';
import {PrivacyPolicyComponent as PagePrivacyPolicyComponent} from './Components/page/privacy-policy/privacy-policy.component';
import {ForgotComponent as PageForgotComponent} from './Components/page/forgot/forgot.component';
import { FaqComponent as PageFaqComponent } from './Components/page/faq/faq.component';
import { FunctionsComponent as PageFunctionsComponent } from './Components/page/functions/functions.component';
import { LearnComponent as PageLearnComponent } from './Components/page/learn/learn.component';
import { StoryComponent as PageStoryComponent } from './Components/page/story/story.component';

import {MainComponent} from './Components/main/main.component';
import { LayoutComponent as MainLayoutComponent } from './Components/main/layout/layout.component';
import { HeaderComponent as MainLayoutHeaderComponent } from './Components/main/layout/header/header.component';
import { FooterComponent as MainLayoutFooterComponent } from './Components/main/layout/footer/footer.component';
import { ProductSearchComponent as MainProductSearchComponent } from './Components/main/product-search/product-search.component';
import { PartsComponent as MainPartsComponent } from './Components/main/parts/parts.component';
import { ExchangeRateComponent as MainExchangeRateComponent } from './Components/main/parts/exchange-rate/exchange-rate.component';
import { RailsService } from './Services/Shared/rails.service';

@NgModule({
    declarations: [
        AppComponent,
        PageComponent,
        PageLayoutHeaderComponent,
        PageLayoutFooterComponent,
        PageHomeComponent,
        PageContactComponent,
        PageSignupComponent,
        PageLoginComponent,
        PageTermsComponent,
        PageLawComponent,
        PagePrivacyPolicyComponent,
        PageForgotComponent,
        PageFaqComponent,
        PageFunctionsComponent,
        PageLearnComponent,
        PageStoryComponent,
        MainComponent,
        MainLayoutComponent,
        MainLayoutHeaderComponent,
        MainLayoutFooterComponent,
        MainProductSearchComponent,
        MainPartsComponent,
        MainExchangeRateComponent,
    ],
    imports: [
        BrowserModule,
        FormsModule,
        HttpModule,
        JsonpModule,
        MdlModule,
        MdlDatePickerModule,
        MdlExpansionPanelModule,
        MdlPopoverModule,
        MdlSelectModule,
        RouterModule,
        RouterModule.forRoot(routes)
    ],
    providers: [RailsService],
    bootstrap: [AppComponent]
})
export class AppModule { }



