import { Injectable } from '@angular/core';
import { ProductSearchModule } from "../../Modeles/Product/Search/product-search.module";
import { RailsService } from "../Shared/rails.service";
import { Http, Response, Headers, RequestOptions, Jsonp, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs';



const SEARCH = 'product/search';       // POST



// 商品管理
@Injectable()
export class ProductService {

  // コンストラクタ
  constructor(
    public http: Http,
    private rails: RailsService) { }



  // 検索
  public Search(searchCondition: ProductSearchModule): Observable<Response> {
    let post_data = JSON.stringify({
      // Todo 検索条件
    })

    let headers = new Headers({
      'Content-Type': 'application/json',
      'X-CSRF-Token': this.rails.getCsrfToken()
    })

    let options = new RequestOptions({ headers: headers })

    return this.http.post(SEARCH, post_data, options)
  }
}



