import { BaseAccount } from '../../Modeles/Account/base-account';
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, Jsonp, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs';
import { RailsService } from '../Shared/rails.service';



const SIGN_IN_API       = 'users/sign_in';       // GET , POST
const SIGN_OUT_API      = 'users/sign_in';       // DELETE
const PASSWORD_NEW_API  = 'users/password/new';  // GET
const PASSWORD_EDIT_API = 'users/password/edit'; // GET
const PASSWORD_API      = 'users/password';      // PATCH , PUT , POST
const CANCEL_API        = 'users/cancel';        // GET
const SIGN_UP_API       = 'users/sign_in';       // GET
const EDIT_API          = 'users/edit';          // GET
const USERS_API         = 'users';               // PATCH , PUT , DELETE , POST



// アカウント管理
@Injectable()
export class AccountService {

  // コンストラクタ
  constructor(
    public http: Http,
    private rails: RailsService) { }



  ////////////////////////////////
  // API
  ////////////////////////////////

  public signIn(account: BaseAccount): Observable<Response> {
    let post_data = JSON.stringify({
      email:    account.email,
      password: account.password
    })

    let headers = new Headers({
      'Content-Type': 'application/json',
      'X-CSRF-Token': this.rails.getCsrfToken()
    })

    let options = new RequestOptions({ headers: headers })

    return this.http.post(SIGN_IN_API, post_data, options)
  }



  public signOut(): Observable<Response> {
    return this.http.delete(SIGN_OUT_API)
  }



  public changePassword(account: BaseAccount): Observable<Response> {
    let post_data = JSON.stringify({
      new_password: account.password
    })

    let headers = new Headers({
      'Content-Type': 'application/json',
      'X-CSRF-Token': this.rails.getCsrfToken()
    })

    let options = new RequestOptions({ headers: headers })

    return this.http.post(PASSWORD_API, post_data, options)
  }



  public changeAccountInfo(account: BaseAccount): Observable<Response> {
    let post_data = JSON.stringify({
      // Todo 変更できるユーザ情報
    })

    let headers = new Headers({
      'Content-Type': 'application/json',
      'X-CSRF-Token': this.rails.getCsrfToken()
    })

    let options = new RequestOptions({ headers: headers })

    return this.http.post(USERS_API, post_data, options)
  }



  public deleteAccount(): Observable<Response> {
    return this.http.delete(USERS_API)
  }
}



