// 為替
export class ExchangeData {
    ask;
    bid;
    currencyPairCode;
    high;
    low;
    open;



    static isCADJPY(data: ExchangeData): boolean {
        return ('CADJPY' === data.currencyPairCode)
    }



    static isUSDJPY(data: ExchangeData): boolean {
        return ('USDJPY' === data.currencyPairCode)
    }



    static isGBPJPY(data: ExchangeData): boolean {
        return ('GBPJPY' === data.currencyPairCode)
    }



    static isEURJPY(data: ExchangeData): boolean {
        return ('EURJPY' === data.currencyPairCode)
    }
}



