import { Component, OnInit } from '@angular/core';

import { ExchangeRateService } from '../../../../Services/Exchange/exchange-rate.service'

@Component({
  selector: 'app-main-parts-exchange-rate',
  templateUrl: './exchange-rate.component.html',
  styleUrls: ['./exchange-rate.component.scss'],
  providers: [ExchangeRateService]
})
export class ExchangeRateComponent implements OnInit {

  public canadaDollar: string = '未取得';
  public usDollar: string     = '未取得';
  public ukpPound: string     = '未取得';
  public eur: string          = '未取得';

  // コンストラクタ
  constructor(public exchangeRateSrv: ExchangeRateService) { }

  ngOnInit() {
    this.exchangeRateSrv.action = (cnd, us, uk, eur) => {
      this.canadaDollar = cnd;
      this.usDollar = us;
      this.ukpPound = uk;
      this.eur = eur;
    };
    // 為替取得開始
    this.exchangeRateSrv.start();
  }

  ngOnDestroy(){
    // 為替取得終了
    this.exchangeRateSrv.stop();
  }

}



