import { MdlSnackbarService } from '@angular-mdl/core/components';
import { NgModule } from '@angular/core';
import { BaseAccount } from '../../../Modeles/Account/base-account';
import { Router } from '@angular/router';
import { AccountService } from '../../../Services/Account/account.service';
import { Component, OnInit } from '@angular/core';

// Todo 追加 バリデート

@Component({
  selector: 'app-page-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  providers: [AccountService]
})
export class LoginComponent implements OnInit {

  isDisplayBtn = true
  isWait       = false
  errMessage: string
  email: string
  password: string

  // コンストラクタ
  constructor(
    public router: Router,
    private accountService: AccountService) { }



  ngOnInit() {
    // Todo 前回の入力値をロード
  }




  // ログインボタン
  public onLoginSubmit() {
    //　エラーを初期化
    this.errMessage = null
    // 待機
    this.wait(true)

    // ログインアカウント
    let account = this.getLoginAccount()
    // ログイン実施
    this.accountService.signIn(account).subscribe(
      res => {
        // Todo ログイン成功時処理


        // 待機解除
        this.wait(false)
      },
      error => {
        // Todo ログイン失敗時処理


        // 待機解除
        this.wait(false)
      })
  }



  // オペレータ作成
  private getLoginAccount(): BaseAccount {
    let account = new BaseAccount()
    account.email    = this.email
    account.password = this.password
    return account
  }



  // 待機
  private wait(status = true) {
    this.isDisplayBtn = !status
    this.isWait       = status
  }
}



