import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductService } from "../../../Services/Product/product.service";
import { ProductSearchModule } from "../../../Modeles/Product/Search/product-search.module";
import { BaseProductData } from "../../../Modeles/Product/ProductData/base-product-data";

@Component({
  selector: 'app-main-product-search',
  templateUrl: './product-search.component.html',
  styleUrls: ['./product-search.component.scss'],
  providers: [ProductService]
})
export class ProductSearchComponent implements OnInit {

  // 待機フラグ
  public isWait = false
  // 商品データ
  public products = Array<BaseProductData>()




  // コンストラクタ
  constructor(
    public router: Router,
    private productService: ProductService) { }



  // 初期化処理
  ngOnInit() {
    // test
    this.products.push(new BaseProductData("AAAA", "aaaa"))
    this.products.push(new BaseProductData("BBBB", "bbbb"))
    this.products.push(new BaseProductData("CCCC", "ccccc"))

    // this.downloadProducts()
  }



  // 商品情報ダウンロード
  private downloadProducts(): void {
    // 現在の商品情報クリア
    this.products = Array<BaseProductData>()
    // 検索条件
    let searchCondition = this.getSearchCondition()

    // ダウンロード開始
    this.productService.Search(searchCondition).subscribe(
      res => {
        // 商品情報更新
        this.products = res.json() as Array<BaseProductData>
        // 待機解除
        this.wait(false)
      },
      error => {
        // Todo 商品情報ダウンロード失敗時処理

        // 待機解除
        this.wait(false)
      })
  }



  // 検索条件取得
  private getSearchCondition(): ProductSearchModule {
    // Todo 検索条件作成
    return new ProductSearchModule
  }



  // 待機
  private wait(status = true) {
    this.isWait = status
  }
}



